.contact{
    padding: 5rem 1rem;
    text-align: center;

    h2{
        margin-bottom: 1rem;
    }

    p{
        margin-bottom: 0.825rem;
    }

    button{
        outline: none;
        border: none;
        background-color: $primary-dark;
        color: $white;
        cursor: pointer;
        font-size: 0.875rem;
        line-height: 1.5625rem;
        font-weight: $fw-lg;
        display: flex;
        flex-direction: row;
        align-items: center;
        margin: auto;
    }

    &__address-icon{
        font-size: 1rem;
        margin-right: 1rem;
    }

    @media screen and (min-width: 375px){
        button{
            font-size: 1.125rem;
        line-height: 1.5625rem;
        }
    }

    @media screen and (min-width: 768px){
        padding: 5rem 1.5rem;

        h2{
            margin-bottom: 2rem;
        }

        p{
            margin-bottom: 1rem;
        }
    }

    @media screen and (min-width: 1024px){
        padding: 5rem 6.25rem;
        text-align: left;
        
        button{
            margin: 0;
        }
    }

    @media screen and (min-width: 1440px){
        max-width: 85rem;
        margin: auto;

        h2{
            margin-bottom: 2.5rem;
            font-size: 2.5rem;
            line-height: 4rem;
            letter-spacing: 0.0444rem;
        }

        p{
            margin-bottom: 1rem;
        }
    }
}