.project{
    position: relative;
    max-width: 100%;

    &__title{
        padding-top: 1rem;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        
        h3{
            position: relative;
        }
    }

    .project__title h3::after{
        content: "";
        position: absolute;
        bottom: 0rem;
        left: 0;
        transition: width 0.2s ease-in;
        background-color: $white;
        width: 0;
        height: 1px;

        @media screen and (min-width: 768px){
            height: 2px;
        }
    }

    &:hover .project__title h3::after{
        width: 100%;
        transition: width 0.2s ease-in;
    }
    
    &__thumbnail, &__overview{
        min-width: 17rem;
        height: 21.25rem;
        max-height: 21.25rem;
    }

    &__thumbnail{
        background-color: $secondary-dark;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        cursor: pointer;

        &--not-ready{
            display: grid;
        }

        &--mobile{
            background-size: contain;
            background-color: $ternary-dark;
        }
    }

    &__thumbnail-tool-icon{
        place-self: center;
        color: $white;
        font-size: 5rem;
    }

    &__overview{
        padding: 0.75rem;
        max-height: 100%;
        background-color: $secondary-dark;
        position: relative;
        cursor: default;
        
        display: grid;
        grid-template-columns: 1fr 3fr 1fr;
        grid-template-rows: 1fr 2fr 4fr 1fr;
        grid-template-areas: 
            ". . close"
            "title title title"
            "overview overview overview"
            "stack stack links";

        @media screen and (min-width: 768px){
            padding: 1.5rem;
        }
        
        p{
            font-size: 0.875rem;
            grid-area: overview;
            align-self: center;
            @media screen and (min-width: 768px){
                font-size: 1rem;
            }
        }
    }

    &__heading{
        grid-area: title;
        justify-self: flex-start;
        align-self: center;
        padding-top: 0.5rem;

        h3{
            margin-bottom: 0.5rem;
            font-size: 1.375rem;
        }

        h4{
            font-size: 1rem;
        }
    }

    &__overview-close-btn{
        grid-area: close;
        justify-self: flex-end;
    }

    &__stack{
        grid-area: stack;
        color: $very-light-gray;
        display: flex;
        flex-direction: row;
        align-items: center;
        font-size: 0.75rem;

        @media screen and (min-width: 768px){
            font-size: 0.875rem;
        }

        li:not(:last-child){
            margin-right: 1rem;
        }
    }

    &__links{
        grid-area: links;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-self: flex-end;
        align-self: center;

        li{
            background-color: $ternary-dark;
            border-radius: 0.25rem;
            display:flex;
            justify-content: center;
            align-items: center;
        }

        li:nth-child(1){
            margin-right: 0.25rem;
        }

        li a{
            padding: 0.5rem;
            color: $very-light-gray;
            font-size: 1.5rem;

            &:hover{
                color: $white;
            }
        }

        .project__link--hidden{
            display: none;
        }
    }
}

#Pomodoro{
    background-image: url("../../images/screenshot-pomodoro.png");
}

#Dine{
    background-image: url("../../images/dine-preview.jpg");
}

#Photosnap{
    background-image: url("../../images/photosnap-screenshot.png");
}

#MyMovies{
    background-image: url("../../images/MyMovies-thumbnail.jpg");
    background-position: flex-start center;
}

#Easybank{
    display: grid;
    background-color: $white;
    .inner{
        background-image: url("../../images/easybank-logo.svg");
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        width: 8.6875rem;
        height: 1.25rem;
        place-self: center;
        transform: scale(1.75);
    }
}